.drawer 
{
  position: fixed;
  top: 0;
  height: 100vh;
  overflow: auto;
  background: white;
  z-index: 1999;
}

.projects-drawer 
{
  color: black;
  background-color: #FFFFFFBF;
  top: 55px;
  height: calc(100% - 55px);
  left: 0;
  width: 30vw;
  padding: 0;
  padding-bottom: 30px;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: all 400ms cubic-bezier(0.59, 0.07, 0.52, 0.96);
  -o-transition: all 400ms cubic-bezier(0.59, 0.07, 0.52, 0.96);
  transition: all 400ms cubic-bezier(0.59, 0.07, 0.52, 0.96);
  padding-top: 10vh;
  padding-left: 4vw;
}

.thumbnail-drawer 
{
  color: white;
  background: black;
  left: auto;
  right: 0;
  width: 70vw;
  top: 55px;
  height: calc(100% - 55px);
  padding: 0;
  padding-bottom: 30px;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: all 400ms cubic-bezier(0.59, 0.07, 0.52, 0.96);
  -o-transition: all 400ms cubic-bezier(0.59, 0.07, 0.52, 0.96);
  transition: all 400ms cubic-bezier(0.59, 0.07, 0.52, 0.96);
}

.thumbnail-drawer-closed
{
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}

.projects-drawer-closed
{
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}

.thumbnail-drawer-button
{
  position: fixed;
  right: 3vw;
  top: 9vh;
  visibility: visible;
  width: 32px;
  height: 32px;
  cursor: pointer;
  z-index: 2000;
  -webkit-transition: all 200ms;
  -o-transition: all 200ms;
  transition: all 200ms;
}

.thumbnail-drawer-button:hover
{
  opacity: 0.7;
}

