  z-index: 0;
}

.close-thumbnail-drawer-button
{
  background-image: url("data:image/svg+xml;utf8,<svg fill='#FFFFFF' height='32' viewBox='0 0 24 24' width='32' xmlns='http://www.w3.org/2000/svg'><path d='M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
}

.projects-drawer-button
{
  position: fixed;
  left: 3vw;
  top: 9vh;
  visibility: visible;
  width: 32px;
  height: 32px;
  cursor: pointer;
  z-index: 2000;
  -webkit-transition: all 200ms;
  -o-transition: all 200ms;
  transition: all 200ms;
