.photo-project-overview
{
  margin-top: 30px;
  margin-left: 3vw;
  margin-right: 30px;
}

.thumbnail-project-label
{
  font-weight: 300;
  margin-bottom: 15px;
  margin-left: 2px;
  font-size: 16px;
}

.thumbnail
{
  height: 75px;
  margin: 3px;
  width: auto;
  cursor: pointer;
}

.thumbnail:hover
{
  opacity: 0.8;
}

.photo-projects-section
{
  margin-bottom: 5vh;
}

.photo-projects-section-label
{
  font-weight: 700;
  font-size: 16px;
  margin-bottom: 12px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.photo-projects-label
{
  margin-left: 3vw;
  font-weight: 300;
  font-size: 22px;
  margin-bottom: 10px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.photo-projects-label:hover
{
  opacity: 0.6;
}

.photo-frame
{
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -55%);
      -ms-transform: translate(-50%, -55%);
          transform: translate(-50%, -55%);
  z-index: 1000;
  max-width: 75%;
  max-height: 65%;
  -webkit-box-shadow: 0px 0px 10px 3px #E8E8E8;
          box-shadow: 0px 0px 10px 3px #E8E8E8;
}

.frame-count {
  position: fixed;
  text-align: center;
  font-size: 17px;
  bottom: 12%;
  width: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-weight: 300;
}

.footer-label {
  text-align: center;
  z-index: 1000;
  width: 100%;
  position: fixed;
  bottom: 7%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: inline-block;
  white-space: pre;
  font-weight: 300;
  font-size: 22px
}

.label-nav {
  cursor: pointer;
}

.label-nav-disabled {
  color: #DDDDDD;
  cursor: default;
}


@media (max-width:768px) {
  .label-nav 
  {
    display: inline-block;
    white-space: pre;
    font-size: 20px;
  }

  .frame-count 
  {
    font-size: 15px;
  }

  .thumbnail-drawer 
  {
    width: 100%;
  }

  .projects-drawer 
  {
    width: 100%;
    padding-left: 20vw;
  }
}


